
.whiteframe-z1{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2,.v-select-option-container{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
.v-select-container{position:relative;margin:16px 0
}
.v-select-container label{display:block;position:absolute;transform:translateY(0px);transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1);color:rgba(0,0,0,0.54);pointer-events:none;font-size:16px
}
.v-select-container.focus label,.v-select-container.has-value label{transform:translateY(-24px);font-size:12px
}
.v-select-container.focus label{color:#3F51B5
}
.v-select-container.focus input,.v-select-container.focus textarea{border-bottom:2px solid #3F51B5;margin-bottom:15px
}
.v-select{align-items:center;display:inline-flex;padding:4px 4px;cursor:pointer;font-size:16px;width:100%;border-width:0 0 1px;border-style:solid;border-color:rgba(0,0,0,0.12);height:28px
}
.v-select i{position:absolute;right:16px;opacity:.54
}
.v-select-option-container{position:absolute;top:-19px;left:-12px;z-index:10;background-color:#fff;padding:8px 0;min-height:48px;max-height:256px;overflow:auto;width:100%
}
.v-select-option{font-size:16px;height:48px;display:flex;align-items:center;padding:0 16px;cursor:pointer;transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)
}
.v-select-option:hover{background-color:#EEEEEE
}
.v-select-backdrop{position:fixed;height:100vh;width:100vw;top:0;left:0;z-index:5
}



