
.v-input-container{margin:16px 0;position:relative;width:100%
}
.v-input-container label{display:block;position:absolute;transform:translateY(4px);transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1);color:rgba(0,0,0,0.54);pointer-events:none;font-size:16px
}
.v-input-container input,.v-input-container textarea{background-color:transparent;outline:none;box-sizing:border-box;border-width:0 0 1px;border-style:solid;border-color:rgba(0,0,0,0.12);padding:4px 0;font-size:16px;width:100%;max-width:100%;margin-bottom:16px
}
.v-input-container.focus label,.v-input-container.has-value label{transform:translateY(-16px);font-size:12px
}
.v-input-container.focus label{color:#3F51B5
}
.v-input-container.focus input,.v-input-container.focus textarea{border-bottom:2px solid #3F51B5;margin-bottom:15px
}
::-webkit-input-placeholder,:-moz-placeholder,::-moz-placeholder,:-ms-input-placeholder{color:rgba(0,0,0,0.54)
}



