
.whiteframe-z1,.material-search-bar,.material-autocomplete{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
.material-search-bar{width:100%;background-color:#fff;margin-bottom:16px;padding:16px;font-size:16px;display:flex;align-items:center;position:relative
}
.material-search-bar i{margin-right:16px;cursor:pointer
}
.material-search-bar input{border:none;font-size:16px;outline:none;height:24px;width:100%
}
.material-autocomplete{position:absolute;top:58px;width:100%;background-color:#fff;left:0;border-top:1px solid #eee
}
.material-autocomplete .material-autocomplete-item{padding:16px 16px 16px 56px;border-bottom:1px solid rgba(0,0,0,0.12)
}
.material-autocomplete .material-autocomplete-item:hover{background-color:#EEEEEE
}



