
.v-radio-button-container{width:40px;height:40px;display:inline-block;position:relative;vertical-align:middle;cursor:pointer;user-select:none
}
.v-radio-button-container .circle{width:10px;height:10px;border-radius:50%;position:relative;opacity:0;transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)
}
.v-radio-button-container .v-radio-button{margin:10px;padding:3px;width:20px;height:20px;border-width:2px;border-style:solid;border-radius:50%;border-color:rgba(0,0,0,0.54);vertical-align:middle;transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)
}
.v-radio-button-container.has-value{color:#3F51B5;border-color:#3F51B5
}
.v-radio-button-container.has-value i{opacity:1
}
.v-radio-button-container.has-value .v-radio-button{border-color:#3F51B5
}
.v-radio-button-container.has-value .v-radio-button .circle{opacity:1;background-color:#3F51B5;border-color:#3F51B5
}
.v-radio-button-container.has-value.secondary-color{color:#e91e63
}
.v-radio-button-container.has-value.secondary-color .v-radio-button{border-color:#e91e63
}
.v-radio-button-container.has-value.secondary-color .v-radio-button .circle{background-color:#e91e63;border-color:#e91e63
}



