.whiteframe-z1{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
.paper-ripple{display:block;position:absolute;top:0;left:0;right:0;bottom:0;border-radius:inherit;overflow:hidden;pointer-events:none
}
.paper-ripple .paper-ripple__background,.paper-ripple .paper-ripple__waves,.paper-ripple .paper-ripple__wave-container,.paper-ripple .paper-ripple__wave{pointer-events:none;position:absolute;top:0;left:0;width:100%;height:100%
}
.paper-ripple .paper-ripple__background,.paper-ripple .paper-ripple__wave{opacity:0
}
.paper-ripple .paper-ripple__waves,.paper-ripple .paper-ripple__wave{overflow:hidden
}
.paper-ripple .paper-ripple__wave-container,.paper-ripple .paper-ripple__wave{border-radius:50%
}
.paper-ripple .paper-ripple__background{will-change:opacity, background-color
}
.paper-ripple .paper-ripple__wave{will-change:opacity, transform
}
.paper-ripple .paper-ripple__wave-container{will-change:transform
}
.paper-ripple--round .paper-ripple__background,.paper-ripple--round .paper-ripple__waves{border-radius:50%
}
.paper-ripple--round .paper-ripple__wave-container{overflow:hidden
}
.fade-enter-active,.fade-leave-active{transition:opacity 0.3s cubic-bezier(0, 0, 0.2, 1);opacity:1;display:block
}
.fade-enter,.fade-leave-active{opacity:0
}
.hidden{opacity:0
}
.visibility-hidden{visibility:hidden
}
.toast-enter-active,.toast-leave-active{transition:transform 0.4s cubic-bezier(0, 0, 0.2, 1);transform:translateY(0px)
}
.toast-enter-active span,.toast-leave-active span{transition:opacity 0.4s cubic-bezier(0, 0, 0.2, 1) 0.1s;opacity:1
}
.toast-enter,.toast-leave-active{transform:translateY(48px)
}
.toast-enter span,.toast-leave-active span{opacity:0
}
.headings{border-bottom:1px solid rgba(0,0,0,0.12);padding:0 0 16px 0
}
.no-margin{margin:0 0 0 0
}
.typography-demo .row{margin-bottom:16px
}
