
.v-tabs{width:100%
}
.v-tabs.primary>.v-tab-items-container{background-color:#3F51B5
}
.v-tabs.primary>.v-tab-items-container .active-tab-bar{background-color:#fff
}
.v-tabs.primary>.v-tab-items-container button{color:#fff
}
.v-tabs.secondary>.v-tab-items-container{background-color:#e91e63
}
.v-tabs.secondary>.v-tab-items-container .active-tab-bar{background-color:#fff
}
.v-tabs.secondary>.v-tab-items-container button{color:#fff
}
.v-tabs>span{line-height:24px;white-space:nowrap
}
.v-tabs.icons>.v-tab-items-container .v-tab-items>button,.v-tabs.icons>.v-tab-items-container .v-tab-items>button span{line-height:14px
}
.v-tabs.icons-only .v-tab-items-container .v-tab-items i{margin-bottom:0
}
.v-tabs .v-tab-items-container .v-tab-items i{min-width:24px;min-height:24px
}
.v-tab-items-container{overflow:hidden
}
.v-tab-items-container span{width:80px;text-overflow:ellipsis;overflow:hidden;display:block
}
.v-tab-items-container.primary>{background-color:#3F51B5
}
.v-tab-items-container.primary>button{color:#fff
}
.v-tab-items-container.secondary>{background-color:#e91e63
}
.v-tab-items-container.secondary>button{color:#fff
}
.v-tab-items-container .v-tab-items{display:flex;transition:transform 0.2s cubic-bezier(0, 0, 0.2, 1);position:relative
}
.v-tab-items-container .v-tab-items button{margin:0;padding:12px 24px;border-radius:0
}
.v-tab-items-container .v-tab-items.panning{transition:none
}
.v-tab-items-container .v-tab-items i{display:block;margin-bottom:10px
}
.v-tab-items{display:inline-block
}
.active-tab-bar{width:100px;height:2px;background-color:#e91e63;position:relative;top:-2px;transform-origin:0px 0px 0px;transition:all 0.2s cubic-bezier(0, 0, 0.2, 1);z-index:5;margin-bottom:-2px
}
.active-tab-bar.panning{transition:none
}



