
.whiteframe-z1,.material-data-table,.material-data-table .v-data-table-edit-container{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
.material-data-table{width:100%;background-color:#fff
}
.material-data-table .material-data-table-header{height:64px;display:flex;align-items:center;padding:0 24px
}
.material-data-table table{border-top:1px solid rgba(0,0,0,0.12);border-spacing:0;border-collapse:separate
}
.material-data-table th{font-size:12px;text-align:right;font-weight:500;color:rgba(0,0,0,0.54);height:56px;border-bottom:1px solid rgba(0,0,0,0.12);vertical-align:middle;white-space:nowrap
}
.material-data-table th .v-order-by{font-size:16px;font-weight:600;transition:all 0.25s cubic-bezier(0.55, 0, 0.1, 1);vertical-align:middle;right:8px;position:relative
}
.material-data-table th .v-order-by.ascending{transform:rotate(180deg)
}
.material-data-table th span{vertical-align:middle;cursor:pointer
}
.material-data-table tr,.material-data-table tr td{transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)
}
.material-data-table.bordered td{border-bottom:1px solid rgba(0,0,0,0.12)
}
.material-data-table.striped tbody tr:nth-child(odd){background-color:#f5f5f5
}
.material-data-table td{height:48px;text-align:right;font-size:13px;color:rgba(0,0,0,0.87);position:relative
}
.material-data-table td,.material-data-table th{padding-right:56px;border-right:none;border-left:none;border-top:none;user-select:none
}
.material-data-table th.v-first-col,.material-data-table td.v-first-col{padding-left:24px;padding-right:0;text-align:left
}
.material-data-table th.v-first-col i,.material-data-table td.v-first-col i{margin-left:8px;margin-right:0;right:0
}
.material-data-table tr th.first-col i{margin-left:8px;margin-right:0;right:0
}
.material-data-table tr th:last-child,.material-data-table tr td:last-child{padding-right:24px
}
.material-data-table.hover tr:hover td{background-color:#EEEEEE
}
.material-data-table .checkbox i{display:block
}
.material-data-table .v-selection{width:56px;padding:0
}
.material-data-table .v-column-options .v-select-container{width:152px;float:right
}
.material-data-table .v-editable{cursor:pointer
}
.material-data-table .v-editable span{border-bottom:1px dashed rgba(0,0,0,0.54);cursor:pointer;right:48px;position:relative
}
.material-data-table .v-data-table-edit-container{position:absolute;left:230px;z-index:12;background-color:#fafafa;padding:16px 16px 0 16px
}
.material-data-table .v-data-table-edit-container .v-input-container{margin:1px
}
.material-data-table .v-edit-buttons{float:right
}
.material-data-table .v-edit-buttons .cancel{margin-left:0
}
.material-data-table .v-edit-buttons .save{margin-right:0
}
.material-data-table .v-table-edit-icon{position:absolute;right:56px;top:18px;color:rgba(0,0,0,0.54)
}
.material-data-table .v-results-message{text-align:center
}
@media screen and (max-width: 768px){
.material-data-table.responsive{box-shadow:none
}
.material-data-table.responsive .material-data-table-container{overflow:visible
}
.material-data-table.responsive table{margin-bottom:0;background-color:transparent
}
.material-data-table.responsive table>thead,.material-data-table.responsive table>tfoot{display:none
}
.material-data-table.responsive table>tbody{display:block
}
.material-data-table.responsive table>tbody>tr{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12);display:block;border-radius:2px;margin-bottom:16px
}
.material-data-table.responsive table>tbody>tr>td{background-color:#fff;display:block;vertical-align:middle;text-align:right;padding:16px
}
.material-data-table.responsive table>tbody>tr>td:hover{background-color:#EEEEEE
}
.material-data-table.responsive table>tbody>tr>td[data-title]:before,.material-data-table.responsive table>tbody>tr>td[data-title]:before{content:attr(data-title);font-weight:500;float:left;padding-right:16px
}
}
.v-table-edit-backdrop{position:fixed;height:100vh;width:100vw;top:0;left:0;z-index:11
}
h2{font-size:20px
}
table{width:100%;table-layout:fixed
}



