
.checkbox-container{width:40px;height:40px;display:inline-block;position:relative;vertical-align:middle;cursor:pointer;user-select:none
}
.checkbox-container i{position:absolute;top:0;left:0;margin:12px;font-size:16px;opacity:0;color:#fff;transition:all 0.25s cubic-bezier(0, 0, 0.2, 1);font-weight:700
}
.checkbox-container .checkbox{margin:10px;width:20px;height:20px;border-width:2px;border-style:solid;border-radius:2px;border-color:rgba(0,0,0,0.54);vertical-align:middle;transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)
}
.checkbox-container.has-value i{opacity:1
}
.checkbox-container.has-value .checkbox{background-color:#3F51B5;border-color:#3F51B5
}
.checkbox-container.has-value.secondary-color .checkbox{background-color:#e91e63;border-color:#e91e63
}



