
.v-chip-container[data-v-35]{border-bottom:1px solid rgba(0,0,0,0.12);padding:4px 0;display:flex;flex-wrap:wrap
}
.v-chip-container input[data-v-35]{background-color:transparent;outline:none;border:none;padding:4px 0;font-size:14px;max-width:100%;height:40px;flex-grow:1
}
.v-chip-container.focused[data-v-35]{border-bottom:2px solid #3F51B5;margin-bottom:-1px
}
.v-chip[data-v-35]{background-color:#e0e0e0;border-radius:16px;margin:4px 8px 4px 0;padding:0 12px;height:32px;display:inline-block;font-size:13px;line-height:32px
}
.v-chip span[data-v-35]{vertical-align:middle
}
.chip-remove[data-v-35]{border-radius:50%;background-color:rgba(0,0,0,0.54);margin:0 -4px 0 8px;vertical-align:middle;color:#e0e0e0;font-size:14px;padding:2px;font-weight:600;cursor:pointer;opacity:.54;width:18px;height:18px
}



