
.whiteframe-z1{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2,.toolbar{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
.toolbar{width:100%;height:40px;display:flex;align-items:center;position:relative;color:#fff;background-color:#3F51B5;padding:4px
}
.toolbar .icon-button-wrapper{width:40px;height:40px
}
.toolbar i{color:#fff;display:inline
}
.toolbar .button.icon-button.square,.toolbar i{height:40px;width:40px
}
.toolbar .title{position:relative;margin-left:16px
}



