.v-button{padding:8px 16px;position:relative;outline:none;font-size:14px;font-weight:500;text-transform:uppercase;border:none;border-radius:2px;line-height:20px;background-color:transparent;cursor:pointer;transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1);margin:4px;user-select:none;z-index:1
}
.v-button.disabled{cursor:not-allowed;opacity:.54;pointer-events:none
}
.v-button.flat.primary{color:#3F51B5
}
.v-button.flat.secondary{color:#e91e63
}
.v-button.raised.primary{color:#fff;background-color:#3F51B5
}
.v-button.raised.secondary{color:#fff;background-color:#e91e63
}
.v-button.v-icon-button{border-radius:50%;margin:0;height:40px;width:40px;padding:0
}
.v-button.v-icon-button i{vertical-align:middle
}
.v-button.v-icon-button.small{height:40px;width:40px
}
.v-button.v-icon-button.square{height:48px;width:48px;border-radius:2px;-webkit-mask-image:none
}
.v-icon-button-container{display:inline-block;border-radius:50%;height:40px;width:40px;margin:4px
}
.v-icon-button-wrapper{border-radius:50%;height:40px;width:40px;display:inline-block;margin-right:8px;position:relative;-webkit-mask-image:url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAIAAACQd1PeAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAA5JREFUeNpiYGBgAAgwAAAEAAGbA+oJAAAAAElFTkSuQmCC)
}
.v-icon-button-wrapper.small{height:40px;width:40px
}
.v-icon-button-wrapper.flat.primary,.v-icon-button-wrapper.flat.primary i{color:#3F51B5
}
.v-icon-button-wrapper.flat.secondary,.v-icon-button-wrapper.flat.secondary i{color:#e91e63
}
.v-icon-button-wrapper.raised.primary,.v-icon-button-wrapper.raised.primary i{color:#fff
}
.v-icon-button-wrapper.raised.primary{background-color:#3F51B5
}
.v-icon-button-wrapper.raised.secondary,.v-icon-button-wrapper.raised.secondary i{color:#fff
}
.v-icon-button-wrapper.raised.secondary{background-color:#e91e63
}
.list{transition:all 0.25s cubic-bezier(0.55, 0, 0.1, 1)
}
.secondary-list .list{margin:0
}



