
.whiteframe-z1{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)
}
.whiteframe-z2{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)
}
.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.whiteframe-z5,.v-dialog-container .v-dialog{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)
}
.fade-enter-active,.fade-leave-active{transition:opacity 0.3s cubic-bezier(0, 0, 0.2, 1);opacity:1;display:block
}
.fade-enter,.fade-leave-active{opacity:0
}
.hidden{opacity:0
}
.visibility-hidden{visibility:hidden
}
.toast-enter-active,.toast-leave-active{transition:transform 0.4s cubic-bezier(0, 0, 0.2, 1);transform:translateY(0px)
}
.toast-enter-active span,.toast-leave-active span{transition:opacity 0.4s cubic-bezier(0, 0, 0.2, 1) 0.1s;opacity:1
}
.toast-enter,.toast-leave-active{transform:translateY(48px)
}
.toast-enter span,.toast-leave-active span{opacity:0
}
.v-dialog-container{display:flex;position:fixed;top:0;right:0;align-items:center;height:100vh;width:100vw;z-index:11;pointer-events:none
}
.v-dialog-container.show{pointer-events:auto
}
.v-dialog-container .v-dialog{margin:auto;min-width:280px;background-color:#fff;max-width:80%;z-index:10;max-height:80%;overflow:hidden
}
.v-dialog-container .v-dialog .toolbar{box-shadow:none
}
.v-dialog-container .v-dialog .v-dialog-content{margin:20px 0 24px 0;padding:0 24px;max-height:calc(80vh - 68px);overflow-x:hidden;overflow-y:auto
}
.v-dialog-container .v-dialog.has-actions .v-dialog-content{max-height:calc(80vh - 68px - 44px);margin-bottom:53px
}
.v-dialog-container .v-dialog .v-dialog-title{margin:24px 24px 20px 24px;padding:0
}
.v-dialog-container .v-dialog.dialog-enter-active,.v-dialog-container .v-dialog.dialog-leave-active{opacity:1;transform:translateY(0px);transition:all 0.3s cubic-bezier(0, 0, 0.2, 1)
}
.v-dialog-container .v-dialog.dialog-enter,.v-dialog-container .v-dialog.dialog-leave-active{opacity:0;transform:translateY(50px)
}
.v-dialog-container .v-dialog .v-dialog-actions-container{padding:4px;width:100%;background-color:#fff;bottom:0;border-top:1px solid rgba(0,0,0,0.12)
}
.v-dialog-container .v-dialog .v-dialog-actions{float:right
}
.v-dialog-container .v-dialog .v-dialog-actions .v-button{min-width:64px;padding:0 8px 0 8px;height:36px
}
.v-dialog-container .v-dialog.small{width:280px
}
.v-dialog-container .v-dialog.medium{width:480px
}
.v-dialog-container .v-dialog.large{width:768px
}
.v-dialog-container .v-dialog-backdrop{z-index:10;position:fixed;height:100vh;width:100vw;background-color:rgba(0,0,0,0.56)
}
.v-dialog-container .v-dialog-backdrop.fade-enter-active,.v-dialog-container .v-dialog-backdrop.fade-leave-active{opacity:1;transition:all 0.3s cubic-bezier(0, 0, 0.2, 1)
}
.v-dialog-container .v-dialog-backdrop.fade-enter,.v-dialog-container .v-dialog-backdrop.fade-leave-active{opacity:0
}



